.App {
  text-align: center;
}

article {
  font-size: 1.2rem;
  width: 90%;
  margin: 1rem;
}

.column {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: left;
}
img {
    width: 100px;
    border-radius: 50%;
    margin-top: 3rem;
    margin-left: auto;
    margin-right: auto;
    display: block;
  }
.playbutton {
    width: 100px;
    height: 50px;
    margin-top: 2rem;
    margin-left: auto;
    margin-right: auto;
    display: block;
  }
